import Icon, { VariantType } from '@cetus/ui-kit/src/components/Icon'
import { Flex, HTMLChakraProps } from '@chakra-ui/react'

interface IconBgProps extends HTMLChakraProps<'div'> {
  xlinkHref: string
  svgHover?: string
  svgFill?: string
  variant?: VariantType
  onClick?: (e: any) => void
}
export default function IconBg(props: IconBgProps) {
  const { svgHover = 'text_caption', svgFill = 'text_paragraph', xlinkHref, onClick, variant = 'solid', ...rest } = props
  return (
    <Flex
      bg="bg_secondary"
      borderRadius="12px"
      w="40px"
      h="40px"
      alignItems="center"
      justifyContent="center"
      border="1px solid"
      borderColor="border"
      cursor="pointer"
      {...rest}
      onClick={onClick}
    >
      <Icon svgHover={svgHover} svgFill={svgFill} xlinkHref={xlinkHref} variant={variant} />
    </Flex>
  )
}
